<template>
    <div id="main" style="width: 600px;height:400px;"></div>
</template>
<script>
import * as echarts from 'echarts';
export default {
    mounted() {
        var chart = echarts.init(document.getElementById('main'));
        var option = {
            title: {
                text: 'ECharts 引入:一个月的事务代办事项总结'
            },
            tooltip: {},
            xAxis: {
                data: ["上课", "睡觉", "运动", "游戏", "吃饭", "洗澡"]
            },
            yAxis: {},
            series: [{
                name: '添加次数',
                type: 'bar',
                data: [24, 30, 12, 30, 28, 21]
            }]
        };
        chart.setOption(option);
    }
}
</script>